// API
import Vue from 'vue'
import Vuex from 'vuex'
// storejs
import mutations from './mutations.js'
import actions from './actions.js'
// 
Vue.use(Vuex)
// Store
const store = new Vuex.Store({
  // 
  state: {
    // 
    topics: [
          // {name: 'all', desc: ''},
          // {name: 'good', desc: '', selected: false},
          // {name: 'share', desc: '', selected: false},
          // {name: 'ask', desc: '', selected: false},
          // {name: 'job', desc: '', selected: false},
          // {name: 'dev', desc: '', selected: false}
          {name: 'all', desc: ''},
          {name: 'good', desc: ''},
          {name: 'share', desc: ''},
          {name: 'ask', desc: ''},
          {name: 'job', desc: ''},
          {name: 'dev', desc: ''}
    ],
    // 
    unLoginedmoreMenus: {
      login: ''
    },
    // 
    loginedMoreMenus: {
      create: '',
      center: '',
      msg: '',
      logout: ''
    },
    // 
    isLogined: false,
    // 
    loginname: '',
    // 
    isShowMoreMenus: false,
    // accesstoken
    accesstoken: '',
    // id
    author_id: ''
  },
  // 
  getters: {
    moreMenus: function (state) {
      if (state.isLogined) {
        // 
        return state.loginedMoreMenus
      }
      return state.unLoginedmoreMenus
    }
  },
  mutations,
  actions
})
// 
export default store



